@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

nav {
  border-bottom: #dedede 1px solid;
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
  background: white;
  text-align: left;
  display: flex;
  justify-content: space-between;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: inline-block;
}

nav ul:nth-child(2) {
  text-align: right;
}

nav li {
  display: inline-block;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

nav a {
  text-decoration: none;
  color: black;
  font-weight: bold;
  padding: 1rem 2rem;
}

nav a:hover {
  color: #ad686a;
  text-decoration: none;
}

.content {
  margin-top: 10rem;
}

.projects-list-card {
  margin-left: auto;
  margin-right: auto;
  width: 50%;
  margin-bottom: 10rem;
}
.projects-list-card h1 {
  display: inline-block;
}

.projects-list-card img {
  object-fit: contain;
  max-width: 100%;
  max-height: auto;
}

.projects-list-card .image-container {
  /* height: 20rem; */
  width: 100%;
  overflow: hidden;
}

.project-preview p {
  text-align: left;
  width: 50%;
  margin-top: 1rem;
  display: block;
}
.project-preview h1 {
  width: 50%;
  margin-top: 1rem;
  font-weight: 300;
}
.project-preview {
  display: flex;
  justify-content: space-between;
}

.more-button {
  display: inline-block;
}

.more-button a {
  display: inline-block;

  text-decoration: none;
  color: black;
  padding: 0.75rem;
  border: 1px #d8d8d8 solid;
  border-radius: 5px;
  margin-top: 1rem;
}

.more-button a:hover {
  text-decoration: underline;
  background-color: #e9e9e9;
}

.content {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.project-page-title h1 {
  display: inline-block;
  width: 50%;
  margin-top: 1rem;
  color: #ab5666;
  font-weight: 300;
  font-size: 3rem;
}

.project-page-title p {
  display: inline-block;
  width: 50%;
  margin-top: 1.5rem;
}

.project-page-title {
  display: flex;
  justify-content: space-between;
}

